/**
 * Javascript controller for the "Grading" panel at the right of the page.
 *
 * @module     mod_assign/grading_panel
 * @copyright  2016 Damyon Wiese <damyon@moodle.com>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 * @since      3.1
 */
define(['jquery', 'core/yui', 'core/notification', 'core/templates', 'core/fragment',
        'core/ajax', 'core/str', 'format_udehauthoring/test'],
    function($, Y, notification, templates, fragment) {

        /**
         * GradingPanel class.
         *
         * @class mod_assign/grading_panel
         */
        var Test = function() {
            // eslint-disable-next-line no-debugger
            debugger;
            this.registerEventListeners();
        };

        /**
         * Get the user context - re-render the template in the page.
         *
         * @private
         * @method _refreshGradingPanel
         */
        Test.prototype._refreshGradingPanel = function() {
            // eslint-disable-next-line no-debugger
            debugger;
            fragment.loadFragment('format_udehauthoring', 'gradingpanel');
        };


        Test.prototype.registerEventListeners = function() {
            let formContainer = document.getElementById('form_container');
            // eslint-disable-next-line no-debugger
            debugger;
            formContainer.on('click', this._refreshGradingPanel.bind(this));

        };

        return Test;
    });
